                label: 'Clientes por Reparto',
                data: [],
                backgroundColor: [
                    'rgb(54, 162, 235)',
                    'rgb(255, 205, 86)',
                    'rgb(0, 0, 0)',
                    'rgb(255, 165, 0)',
                    'rgb(137, 30, 53)'
                ],
                hoverOffset: 4
            };
            this.datarep.forEach((element) => {
                this.datachart.labels.push('reparto ' + element.id_reparto);
                let opcionrepacopia = {
                    id_reparto: -1,
                    lista_id: [],
                    tabla_clientes: [],
                };
                opcionrepacopia.id_reparto = element.id_reparto
                element.clientes_reparto.forEach(elemento => opcionrepacopia.lista_id.push(parseInt(elemento.split("/")[4])));
                console.log(opcionrepacopia.lista_id);
                for (var i = 0; i < data.results.length; i++) {
                    if (opcionrepacopia.lista_id.includes((data.results)[i].numero_cliente))
