                        opcionrepacopia.tabla_clientes.push((data.results)[i])
                    }
                }
                let totalclis = opcionrepacopia.lista_id.length;
                datasetear.data.push(totalclis);
                this.datachart.datasets.push(datasetear);
                this.repartoslista.push(opcionrepacopia);
