    fechaactual() {
        const f = new Date();
        console.log(f.toLocaleDateString());
        const formatDate = (d) => {
            return ('0' + d.getDate()).slice(-2) + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + d.getFullYear()
        }
        console.log(formatDate(f));
        return (formatDate(f));
    },
    async buscarnow() {
      // Declare variables
      var input,
        filter,
        table,
        tr,
        td,
        i,
        txtValue,
        p1, //nro de orden
        p2, //nro de socio
        p3, //servicio
        p4, //id medico
        p5; //fecha
      input = this.$refs.buscadorlista;
      filter = input.value.toUpperCase();
      table = document.getElementById("tablaregistros");
      tr = table.getElementsByTagName("tr");

      // Loop through all list items, and hide those who don't match the search query
      for (i = 1; i < tr.length; i++) {
        td = tr[i].getElementsByTagName("td");
        p1 = td[0].textContent || td[0].innerText;
        p2 = td[1].textContent || td[1].innerText;
        p3 = td[3].textContent || td[3].innerText;
        p4 = td[4].textContent || td[4].innerText;
        p5 = td[6].textContent || td[6].innerText;
        txtValue = p1 + p2 + p3 + p4 + p5;
        if (txtValue.toUpperCase().indexOf(filter) > -1) {
          tr[i].style.display = "";
        } else {
          tr[i].style.display = "none";
        }
      }
    },
